import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout/Layout";
import { Grid, Tooltip } from "@material-ui/core"
// import FrontPageBlurbs from "../components/frontpageblurbs"
// import { graphql } from 'gatsby'
import '@fontsource/roboto';
import { Build, LibraryBooks, Sms } from "@material-ui/icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faInstagram, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faIdCard } from "@fortawesome/free-solid-svg-icons"
import { ProjectBox } from '../components/projectboxes/ProjectBox';
// import { GatsbyImage } from "gatsby-plugin-image"
import project1 from '../images/riverfish.jpg';
import project2 from '../images/mlr.jpg';
import project3 from '../images/flighttest2.jpg';

import doomIcon from '../images/doom-mobile-hero.jpg';
import tetris from '../images/GoTetris.png';
import superMariota from '../images/supermariotaicon.png';
import pacmanFrog from '../images/pacmanfrogicon.png';
import rushmore from '../images/rushmore.jpg';

import Seo from '../components/Seo';
import { graphql } from 'gatsby';

const IndexPage = ({ data }) => {
	// https://thebestmotherfuckingwebsite.co/
	// https://thebestmotherfucking.website/
	// https://motherfuckingwebsite.com/
	// https://www.reddit.com/r/webdev/comments/mkewwn/this_motherfucking_website_no_seriously_a_must/
	// http://kenwheeler.github.io/

	
	return(
		<Layout>
			<Seo title="Home" />
			<Grid container spacing={2}>
				<Grid item xs={3}>
					<div style={{ marginTop: 'auto', marginBottom: 'auto', fontSize: '21pt' }}>
						<Link to="/" style={{color: `white`, textDecoration: `none` }}>
							<span style={{textTransform: 'uppercase', letterSpacing: '1px'}}>Bryce Egley</span>
							<span style={{fontSize: '11pt', marginLeft: 10}}>(.com)</span>
						</Link>
					</div>
					
					<div style={{ marginTop: 15, marginBottom: 15}}>
						<Link to="/about/" style={{ textDecoration: 'none', color: 'white'}}>
							<ProjectBox height={300} imageSrc={rushmore} boxText="About Me" />
						</Link>
					</div>
					<Grid container spacing={1}>
						<Grid item xs={6}>
							<Link to="/resume/" style={{textDecoration: 'none', color: 'white'}}>
								<div className="hover box flex linkBox" target="_blank">
									<FontAwesomeIcon icon={faIdCard} size="2x" color="white" />
									<div style={{fontSize: '10pt'}}>Resume</div>
								</div>
							</Link>
						</Grid>						
						<Grid item xs={6}>
							<Link to="https://github.com/Mistweaver" target="_blank" style={{textDecoration: 'none'}}>
								<div className="hover box flex linkBox">
									<FontAwesomeIcon icon={faGithub} size="3x" color="green" />
								</div>
							</Link>
						</Grid>
						<Grid item xs={6}>
							<Link to="https://www.linkedin.com/in/bryce-egley-77828411b/" target="_blank" style={{textDecoration: 'none'}}>
								<div className="hover box flex linkBox">
									<FontAwesomeIcon icon={faLinkedin} size="3x" color="blue" />
								</div>
							</Link>
						</Grid>
						<Grid item xs={6}>
							<Link to="https://www.instagram.com/bryceegley/" target="_blank" style={{textDecoration: 'none'}}>
								<div className="hover box flex linkBox">
									<FontAwesomeIcon icon={faInstagram} size="3x" color="purple" />
								</div>
							</Link>
						</Grid>
						
					</Grid>
				</Grid>
				<Grid item xs={9}>
					<div style={{ marginBottom: 85, marginTop: 50}}>
						<Grid container spacing={1}>
							<Grid item xs={2}>
								<Link to="/projects/" target="_blank" style={{textDecoration: 'none'}}>
									<Tooltip title="Project Library">
										<div className="hover box flex" style={{height: '100%'}}>
											<LibraryBooks style={{color: 'white', margin: 'auto'}} />
										</div>		
									</Tooltip>						
								</Link>
							</Grid>
							<Grid item xs={2}>
								<Link to="/games/GoTetris/" style={{textDecoration: 'none', color: 'white'}}>
									<ProjectBox height={125} imageSrc={tetris} boxText=" Game of Thrones Tetris" />
								</Link>
							</Grid>						
							<Grid item xs={2}>
								<Link to="/games/SuperMariotaStadium/" target="_blank" style={{textDecoration: 'none'}}>
									<ProjectBox height={125} imageSrc={superMariota} boxText="Super Mariota Stadium" />
								</Link>
							</Grid>
							<Grid item xs={2}>
								<Link to="/games/Doom/" target="_blank" style={{textDecoration: 'none'}}>
									<ProjectBox height={125} imageSrc={doomIcon} boxText="DOOM" />
								</Link>
							</Grid>
							<Grid item xs={2}>
								<Link to="/games/PacFrog/" target="_blank" style={{textDecoration: 'none'}}>
									<ProjectBox height={125} imageSrc={pacmanFrog} boxText="Pac...frog?" />
								</Link>
							</Grid>
							
						</Grid>
					</div>
					<div style={{display: 'flex', justifyContent: 'space-between', marginBottom: 15, color: 'white'}}>
						<div style={{fontSize: '16pt', fontWeight: 500, marginTop: 'auto'}}>Discover Featured Projects</div>
						<Link to="/projects/" style={{textDecoration: 'none', color: 'white'}}>
							<div className="hover flex" style={{textAlign: 'center', padding: 5, borderRadius: 10}}>
								<Build style={{color: 'orange', marginRight: 10}} />
								<span style={{ marginTop: 'auto', marginBottom: 'auto', color: 'white'}}>See More Projects</span>
							</div>
						</Link>
					</div>
					<Grid container spacing={2}>
						<Grid item xs={4}>
							<Link to="/projects/AquaticLibrary/" target="_blank" style={{textDecoration: 'none'}}>
								<ProjectBox height={200} imageSrc={project1} boxText="The Open Source Aquatic Library" />
							</Link>
						</Grid>
						<Grid item xs={4}>
							<Link to="/projects/RugbyApp/" target="_blank" style={{textDecoration: 'none'}}>
								<ProjectBox height={200} imageSrc={project2} boxText="Major League Rugby App" />
							</Link>
						</Grid>
						<Grid item xs={4}>
							<Link to="/projects/SeniorDesign/" target="_blank" style={{textDecoration: 'none'}}>
								<ProjectBox height={200} imageSrc={project3} boxText="Senior Design" />
							</Link>
						</Grid>
					</Grid>
				</Grid>
			</Grid>
			<div style={{marginBottom: 30, marginTop: 30}}>
				<Grid container spacing={2}>
					<Grid item md={8} xs={12}>
						<div style={{fontSize: '16pt', fontWeight: 500, marginTop: 10, marginBottom: 10, color: 'white'}}>Recent Blurbs</div>
						{
							data.allMarkdownRemark.edges.map(post => (
								<div key={"blurb" + post.node.fields.slug} style={{marginBottom: 10}}>
									<Link to={post.node.fields.slug} style={{ textDecoration: 'none' }}>
										<div className="box hover" style={{ width: '100%', padding: 15 }}>
											<div style={{ fontSize: '9pt', color: 'lightgrey' }}>{post.node.frontmatter.date}</div>
											<div className="titleText secondary">{post.node.frontmatter.title}</div>
											<div className="summaryText">{post.node.frontmatter.summary}</div>
										</div>
									</Link>
								</div>
							))
						}
						<div style={{marginTop: 15}}>
							<Link to="/blurbs/" style={{textDecoration: 'none', color: 'white'}}>
								<div className="hover flex" style={{textAlign: 'center', padding: 5, borderRadius: 10}}>
									<Sms style={{color: 'green', marginRight: 10}} />
									<span style={{ marginTop: 'auto', marginBottom: 'auto', color: 'white'}}>Read More Blurbs</span>
								</div>
							</Link>
						</div>
					</Grid>
					
				</Grid>
			</div>
			{/*<Link to="/my-files/">Look at my files</Link> <br />*/}

			
		</Layout>
	)
}

export default IndexPage

export const query = graphql`
	query {
		allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }, limit: 5) {
			totalCount
			edges {
				node {
					frontmatter {
						date
						title
						summary
						key
					}
					fields {
						slug
					}
				}
			}
		}
	}
`